const input = document.getElementById('randomInput');
const button = document.getElementById('guessButton');
const result = document.getElementById('guessResult');

function generateRandomNumber() {
  return Math.floor(Math.random() * 10) + 1;
}

let randomNumber = generateRandomNumber();

button.addEventListener('click', () => {
  const userGuess = parseInt(input.value);

  if (isNaN(userGuess) || userGuess < 1 || userGuess > 10) {
    result.textContent = '–ë—É–¥—å –ª–∞—Å–∫–∞, –≤–≤–µ–¥—ñ—Ç—å —á–∏—Å–ª–æ –≤—ñ–¥ 1 –¥–æ 10';
    result.style.color = '#900';
    return;
  }

  if (userGuess === randomNumber) {
    result.textContent = `–í—ñ—Ç–∞—é, –≤–∏ –≤–≥–∞–¥–∞–ª–∏ —á–∏—Å–ª–æ! (${randomNumber})`;
    result.style.color = '#039900';
  } else {
    result.textContent = `–í–∏ –ø—Ä–æ–≥—Ä–∞–ª–∏, –∫–æ–º–ø‚Äô—é—Ç–µ—Ä –∑–∞–≥–∞–¥–∞–≤ ${randomNumber})`;
    result.style.color = '#900';
  }

  input.value = ''; 
  randomNumber = generateRandomNumber(); 
});