document.getElementById("checkButton").addEventListener("click", function () {
    const yearInput = document.getElementById("ageInput");
    const year = yearInput.value.trim();
    const resultText = document.getElementById("result");
    const isValidYear = /^\d{4}$/.test(year);
    if (!isValidYear) {
        resultText.textContent = "–ë—É–¥—å –ª–∞—Å–∫–∞, –≤–≤–µ–¥—ñ—Ç—å 4-–∑–Ω–∞—á–Ω–∏–π —Ä—ñ–∫!";
        resultText.style.color = "red";
        return;
    }
    const numYear = parseInt(year);
    const isLeap = (numYear % 4 === 0 && numYear % 100 !== 0) || numYear % 400 === 0;
    if (isLeap) {
        resultText.textContent = "–í–∏ –Ω–∞—Ä–æ–¥–∏–ª–∏—Å—è —É –≤–∏—Å–æ–∫–æ—Å–Ω–∏–π —Ä—ñ–∫!";
        resultText.style.color = "green";
    } else {
        resultText.textContent = "–í–∏ –Ω–∞—Ä–æ–¥–∏–ª–∏—Å—è –Ω–µ —É –≤–∏—Å–æ–∫–æ—Å–Ω–∏–π —Ä—ñ–∫!";
        resultText.style.color = "red";
    }
    yearInput.value = "";
});
